.page__section {
  color: #fcfcfc;
  background-color: #333334;
  margin: 40px;
  align-items: center;
  border: 1px solid hsl(0, 0%, 27%);
  border-top: 1.5px solid hsl(0, 0%, 27%);
  padding: 10px 0px;
  box-shadow: 10px 5px 5px rgb(249, 248, 158);
  gap: 20px;
  padding-left: 10px;
}
.btn {
  width: 100px;
  height: 25px;
  text-align: center;
  background: #fff;

  border: 1px solid hsl(0, 0%, 27%);
  border-top: 1.5px solid hsl(0, 0%, 27%);

  box-shadow: 10px 5px 5px rgb(249, 248, 158);

  padding-left: 10px;
  margin-right: 20px;
  margin-top: auto;
  margin-bottom: 0px;
}

.img {
  width: 200px;
  margin-top: 20px;
}
.link {
  text-decoration: none;
  color: black;
  font-size: 14px;
}
.details {
  margin-left: 20px;
  display: flex;
  gap: 10px;
}
.text {
  font-size: 12px;
}
.list {
  list-style: none;
  display: flex;
}
a:hover,
a:focus {
  text-shadow: 10px 5px 5px rgb(248, 244, 48);
}
